<template>
  <v-card class="root">
    <template v-if="state == 'none'">
      <v-card-text>
        <v-text-field
          ref="localName"
          readonly
          outlined
          :value="url"
          prepend-icon="file_copy"
          @click:prepend="copy()"
        />

        <v-text-field
          prepend-icon="call"
          v-model="connectInput"
          @click:prepend="connect()"
          @keydown="$event.keyCode == 13 && connect()"
        />
      </v-card-text>
    </template>

    <template v-if="state == 'connecting'">
      <div class="name">{{ localName }}</div>
      <div class="name">{{ remoteName }}</div>
    </template>

    <template v-else-if="state == 'connected'">
      <v-card-text>
        <div class="text--primary">
          <p class="ma-0" v-for="(msg, i) in messages" :key="i" :class="msg.class">{{ msg.text }}</p>
        </div>

        <v-text-field hide-details v-model="sendInput" @keydown="$event.keyCode == 13 && send()" />
      </v-card-text>
    </template>
  </v-card>
</template>

<script>
import { FullIdent } from 'web-ident';
import rtc from '@/p2p/rtc';
import { secure, authenticate } from '@/p2p/secure';
import { event } from '@/p2p/common';

const host = location.hostname == 'localhost' ? 'ws://localhost:8081' : 'wss://api.mfro.me/p2p';

function delay(ms) {
  return new Promise(resolve => setTimeout(resolve, ms));
}

export default {
  name: 'home',

  data() {
    let match = /id=([a-zA-Z0-9]+)/.exec(location.search);

    return {
      sendInput: '',
      connectInput: match ? match[1] : '',
      localName: '',
      remoteName: '',
      state: 'none',
      messages: [],
    };
  },

  computed: {
    url() {
      return location.origin + location.pathname + '?id=' + this.localName;
    },
  },

  async created() {
    this.self = await FullIdent.generate();

    let wrtc = await rtc(this.self, { host });
    this.dialer = rtc.dialer(wrtc);

    this.localName = this.self.name;

    let incoming = await this.dialer.accept();
    this.remoteName = incoming.name;
    this.handle(incoming.name, incoming.channel, false);
  },

  methods: {
    async connect() {
      this.state = 'connecting';

      let name = this.connectInput;
      this.remoteName = name;

      let channel = await this.dialer.dial(name);
      this.handle(name, channel, true);
    },

    async handle(name, channel, initiator) {
      channel = await secure(channel, initiator);
      channel = await authenticate(channel, this.self, name);

      this.state = 'connected';
      this.channel = channel;

      if (initiator) {
        let log = [];
        let block = Buffer.alloc(200e3);

        while (true) {
          await channel.send(block);

          let time = performance.now();
          log.push({ block, time });

          let cutoff = time - 10e3;
          while (log[0].time < cutoff) log.shift();

          let total = log.reduce((sum, e) => sum + e.block.length, 0);
          let duration = time - log[0].time;

          this.messages = [
            {
              class: 'send',
              text: `${total} bytes, ${duration.toFixed(0)} ms, ${(total / duration).toFixed(0)} KB/s`,
            },
          ];
        }
      } else {
        let log = [];

        while (true) {
          let time = performance.now();
          let block = await channel.receive();
          log.push({ block, time });

          let cutoff = time - 10e3;
          while (log[0].time < cutoff) log.shift();

          let total = log.reduce((sum, e) => sum + e.block.length, 0);
          let duration = time - log[0].time;

          this.messages = [
            {
              class: 'receive',
              text: `${total} bytes, ${duration.toFixed(0)} ms, ${(total / duration).toFixed(0)} KB/s`,
            },
          ];
        }
      }
    },

    copy() {
      let ref = this.$refs.localName.$refs.input;

      ref.select();
      ref.setSelectionRange(0, this.url.length);

      document.execCommand('copy');
    },

    send() {
      let message = this.sendInput;
      this.sendInput = '';
      this.channel.send(Buffer.from(message));
      this.messages.push({
        class: ['send'],
        text: message,
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.root {
  width: 80ch;
  font-family: monospace;
}

.name {
  font-family: monospace;
  text-align: center;
  padding: 8px 0;
}

p {
  margin: 0 0 1em 0;
  font-family: monospace;

  &.send {
    text-align: right;
  }

  &.receive {
    text-align: left;
  }
}

input {
  width: 100%;
  font-family: monospace;
}
</style>
