import { Sink, Source } from './types';

export default pipe;

function pipe<T = never>(): [Sink<T>, Source<T>] {
    function flush() {
        while (write.length > 0 && read.length > 0) {
            let reading = read.shift()!;
            let writing = write.shift()!;
            reading(writing.value);
            writing.resolve();
        }

        if (complete) {
            for (let callback of read) {
                callback(null);
            }
        }
    }

    let complete = false;
    let read: ((value: T | null) => void)[] = [];
    let write: { value: T | null, resolve: () => void }[] = [];

    return [
        (data) => new Promise((resolve) => {
            if (complete) throw new Error('complete');
            // if (write) throw new Error('overflow');

            if (data == null)
                complete = true;

            write.push({ value: data, resolve });
            flush();
        }),

        () => new Promise((resolve) => {
            if (complete) resolve(null);
            // if (read) throw new Error('overflow');

            read.push(resolve);
            flush();
        }),
    ]
}
