import { Channel } from './common';
import { FullIdent, Ident, base62, Encoder, Decoder } from 'web-ident';

async function stream(key: Buffer, nonce: Buffer) {
    let cryptoKey = await crypto.subtle.importKey('raw', key, {
        name: 'AES-CTR',
        length: key.length * 8,
    }, false, ['encrypt']);

    let blockSize = 16;

    let current = Buffer.alloc(0);
    let counter = Buffer.alloc(blockSize - nonce.length);

    return {
        async process(data: Buffer) {
            let index = 0;
            let result = Buffer.alloc(data.length);
            while (index < data.length) {
                if (current.length == 0) {
                    let blockCount = Math.ceil(data.length / blockSize);

                    current = Buffer.from(await crypto.subtle.encrypt({
                        name: 'AES-CTR',
                        counter: Buffer.concat([nonce, counter]),
                        length: blockSize * 8,
                    }, cryptoKey, Buffer.alloc(blockCount)));

                    let carry = blockCount;
                    for (let i = counter.length - 1; i >= 0 && carry > 0; --i) {
                        let value = counter[i] + carry;
                        counter[i] = value % 256;
                        carry = Math.floor(value / 256);
                    }
                }

                let size = Math.min(current.length, data.length - index);
                for (let i = 0; i < size; ++i) {
                    result[index + i] = data[index + i] ^ current[i];
                }

                index += size;
                current = current.slice(size);
            }

            return result;
        },
    };
}

export async function secure(base: Channel, initiator: boolean): Promise<Channel> {
    const algorithm = {
        name: 'ECDH',
        namedCurve: 'P-521',
    };

    let remote = (async () => {
        let remotePublicRaw = await base.receive();
        if (remotePublicRaw == null) throw new Error('handshake failure');

        let remotePublic = JSON.parse(remotePublicRaw.toString()) as JsonWebKey;
        return await crypto.subtle.importKey('jwk', remotePublic, algorithm, false, []);
    })();

    let keyPair = await crypto.subtle.generateKey(algorithm, false, ['deriveBits']);

    let localPublic = await crypto.subtle.exportKey('jwk', keyPair.publicKey);
    let localPublicRaw = Buffer.from(JSON.stringify(localPublic));
    base.send(localPublicRaw);

    let ecdh = await crypto.subtle.deriveBits({
        name: 'ECDH',
        public: await remote,
    }, keyPair.privateKey, 528);

    let hkdf = await crypto.subtle.importKey('raw', ecdh, { name: 'HKDF' } as any, false, ['deriveBits']);

    let result = await crypto.subtle.deriveBits({
        name: 'HKDF',
        hash: 'SHA-512',
        salt: Buffer.alloc(0),
        info: Buffer.alloc(0),
    } as any, hkdf, (256 + 64) * 2);

    let bits = Buffer.from(result);

    let keys = [bits.slice(0, 32), bits.slice(40, 72)];
    let nonces = [bits.slice(32, 40), bits.slice(72, 80)];

    let streams = await Promise.all(keys.map((key, i) => stream(key, nonces[i])));
    if (!initiator) streams = streams.reverse();

    return {
        async send(data) {
            if (data == null) {
                base.send(null);
                return;
            }

            let cipher = await streams[0].process(data);
            await base.send(cipher);
        },

        async receive() {
            let data = await base.receive();
            if (data == null) return null;

            let plain = await streams[1].process(data);
            return plain;
        },
    };
}

export async function authenticate(base: Channel, self: FullIdent, other: string) {
    let receive = base.receive();

    let local = new Encoder();
    local.bytes(self.publicKey);
    local.bytes(await self.sign(base62.decode(other)));
    base.send(local.result);

    let incoming = await receive;
    if (incoming == null) throw new Error('handshake failure');

    let remote = new Decoder(incoming);
    let remoteKey = await Ident.fromPublicKey(remote.bytes());
    let valid = await remoteKey.verify(self.identity, remote.bytes());
    if (!valid == null) throw new Error('handshake failure');

    return base;
}
